import { TranslateService } from '@ngx-translate/core';
import { DOCUMENT } from '@angular/common';
import { Component, Inject, OnInit, ViewEncapsulation } from '@angular/core';
import { ScrollableComponents } from './scrollable-components.enum';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class HeaderComponent implements OnInit {
  scrolledPage = false;
  showLangMenu = false;
  showBigMenu = false;
  lang = 'english';
  langImg = '../../../../../assets/images/en.jpg';

  ScrollableComponents = ScrollableComponents;
  constructor(
    @Inject(DOCUMENT)
    private doc: Document,
    public translateSer: TranslateService
  ) {}

  ngOnInit(): void {
    // this.fixedNavTop();
    this.changeLang('ar');
  }

  toggleLangMenu() {
    this.showLangMenu = !this.showLangMenu;
  }

  fixedNavTop() {
    window.onscroll = () => {
      if (document.querySelector('html').scrollTop === 0) {
        this.scrolledPage = false;
      } else {
        this.scrolledPage = true;
      }
    };
  }

  toogleBigMenu() {
    this.showBigMenu = !this.showBigMenu;
  }

  scrollToSection(scrollableTarget: ScrollableComponents) {
    const scrollTo = (this.doc.querySelector(
      `[data-scroll="${scrollableTarget}"]`
    ) as HTMLDivElement).offsetTop;
    window.scrollTo({ top: scrollTo - 60, behavior: 'smooth' });
  }

  changeLang(lang) {
    if (lang == 'en') {
      this.translateSer.use('en').subscribe((allTranslation) => {
        this.lang = 'English';
        this.langImg = '../../../../../assets/images/en.jpg';
        document.body.classList.remove('rtl');
      });
    } else {
      this.translateSer.use('ar').subscribe((allTranslation) => {
        this.lang = 'العربية';
        this.langImg = '../../../../../assets/images/ar.jpg';
        document.body.classList.add('rtl');
      });
    }
  }
}
